* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

.navbar {
  height: 10vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 12px;
}
.navbar .hamp {
  width: 50px;
  height: 40px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
  z-index: 100;
}
.navbar .hamp .hamp_links {
  height: 7px;
  width: 90%;
  border-radius: 5px;
  background: #7CAAFF;
  transition: all 0.5s;
}
.navbar .hamp_open .hamp_links {
  background: #000D25;
}
.navbar .hamp_open .hamp_links:nth-child(2) {
  display: none;
  transition: all 0.5s;
}
.navbar .hamp_open .hamp_links:nth-child(1) {
  transform: rotate(45deg) translateY(13px);
}
.navbar .hamp_open .hamp_links:nth-child(3) {
  transform: rotate(-45deg) translateY(-13px);
}
.navbar .hussein_zabi_title {
  font-size: 42px;
  font-family: "Poppins", sans-serif;
  font-weight: bold;
  color: #fff;
  z-index: 100;
}
.navbar .hussein_zabi_title .span {
  color: #7CAAFF;
  transition: all 0.5s;
}
.navbar .hussein_zabi_title .span_open {
  color: #000D25;
  transition: all 0.5s;
}
.navbar .ulbar {
  height: 100vh;
  width: 100%;
  position: fixed;
  top: 0;
  right: 100%;
  background: #7CAAFF;
  padding-top: 10vh;
  display: flex;
  align-items: start;
  flex-direction: column;
  padding-left: 20px;
  transition: all 0.5s;
}
.navbar .ulbar .li_links {
  margin-top: 10px;
}
.navbar .ulbar .li_links .Links {
  color: #fff;
  font-size: 24px;
  font-family: "Poppins", sans-serif;
  font-weight: bold;
}
.navbar .ulbarOpen {
  right: 0%;
}

.main_home {
  min-height: 58vh;
  width: 100%;
  margin: 0;
  padding: 0;
}
.main_home .main_home_container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: relative;
  top: -10vh;
  left: 0;
  min-height: 58vh;
  width: 100%;
  margin: 0;
  padding: 0;
  padding: 20px;
  background: url("../images/main_head_background.svg");
  background-repeat: no-repeat;
  background-position: bottom left;
  background-size: cover;
  z-index: -1;
}
.main_home .main_home_container img {
  width: 60px;
  height: 60px;
  margin-bottom: 20px;
}
.main_home .main_home_container .main_header_continer .im_hussein_zabi {
  font-size: 24px;
  font-family: "Poppins", sans-serif;
  font-weight: bold;
  color: #7CAAFF;
  padding: 0 0 0 20px;
  margin-bottom: 20px;
}
.main_home .main_home_container .main_header_continer .main_ing {
  font-size: 16px;
  font-family: "Poppins", sans-serif;
  color: #fff;
}
.main_home .main_home_container .view_my_work {
  margin-top: 20px;
  position: relative;
  top: 30px;
  height: 38px;
  width: 150px;
  background: none;
  border: 2px solid #7CAAFF;
  color: #7CAAFF;
  border-radius: 10px;
  font-weight: bold;
}

.main_project_section {
  min-height: 100vh;
  width: 100%;
  display: grid;
  grid-template-rows: 0.1fr 1fr 0.05fr;
  grid-row-gap: 10px;
  justify-content: center;
}
.main_project_section .main_title_project {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.main_project_section .main_title_project .projects_title {
  font-size: 36px;
  font-family: "Poppins", sans-serif;
  color: #fff;
  font-style: normal;
  font-weight: bold;
  -webkit-text-fill-color: none;
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: #7CAAFF;
}
.main_project_section .main_3_project_handler {
  min-height: 100vh;
  width: 100%;
  display: flex;
  align-content: center;
  justify-content: space-around;
  padding: 10px 0 10px 0;
  flex-direction: column;
  grid-row-gap: 20px;
}
.main_project_section .main_3_project_handler .project_container {
  background: #000D25;
  height: 400px;
  width: 300px;
  border-radius: 13px;
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  place-items: center;
  padding: 10px;
}
.main_project_section .main_3_project_handler .project_container .main_image_handler img {
  width: 100%;
  height: 100%;
  border-radius: 8px;
}
.main_project_section .main_3_project_handler .project_container .main_info_handler {
  display: grid;
  grid-template-rows: 0.1fr 1fr;
}
.main_project_section .main_3_project_handler .project_container .main_info_handler .project_name {
  font-family: "Poppins", sans-serif;
  color: #fff;
  position: relative;
  top: -10px;
}
.main_project_section .main_3_project_handler .project_container .main_info_handler .project_story {
  font-family: "Poppins", sans-serif;
  color: #fff;
  position: relative;
  top: -5px;
}
.main_project_section .main_3_project_handler .project_container .main_lan_handler {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 50px;
  width: 100%;
}
.main_project_section .main_3_project_handler .project_container .main_lan_handler span {
  color: #fff;
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  padding: 6px;
  border: 1px solid #7CAAFF;
  border-radius: 10px;
}
.main_project_section .main_see_all_projects_section {
  display: flex;
  align-items: center;
  justify-content: center;
}
.main_project_section .main_see_all_projects_section .Link_to_work {
  font-family: "Poppins", sans-serif;
  font-weight: bold;
  font-size: 14px;
  height: 38px;
  width: 150px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 3px solid #7CAAFF;
  border-radius: 7px;
  color: #7CAAFF;
}

.main_footer_section {
  min-height: 60vh;
  display: grid;
  grid-template-rows: 1fr 0.05fr;
}
.main_footer_section .main_get_in_touch {
  background: url("../images/get_in_touch_background.svg");
  background-position: top bottom;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.main_footer_section .main_get_in_touch .main_message {
  height: 30vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.main_footer_section .main_get_in_touch .main_message .main_title {
  color: #fff;
  font-weight: bold;
  font-size: 28px;
  font-family: "Poppins", sans-serif;
  position: relative;
  top: 30px;
}
.main_footer_section .main_get_in_touch .main_message .main_message_paregraph {
  color: #7CAAFF;
  width: 65%;
  position: relative;
  top: 50px;
  text-align: center;
  font-family: "Poppins", sans-serif;
}
.main_footer_section .main_get_in_touch .other_get_in_touch_links {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: start;
  padding-left: 30px;
  position: relative;
  top: 30px;
}
.main_footer_section .main_get_in_touch .other_get_in_touch_links .work_email {
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.main_footer_section .main_get_in_touch .other_get_in_touch_links .work_email .work_link, .main_footer_section .main_get_in_touch .other_get_in_touch_links .work_email .gmail_link {
  font-family: "Poppins", sans-serif;
  color: #7CAAFF;
  font-weight: bold;
}
.main_footer_section .main_right {
  display: flex;
  align-items: center;
  justify-content: center;
}/*# sourceMappingURL=style.css.map */